.login {
    max-width: 1280px;
    width: 100%;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    font-family: 'Inter', 'SuisseIntl', sans-serif;
}

.login__form {
    width: 100%;
    max-width: 396px;
    min-height: 525px;
    display: flex;
    flex-direction: column;
    gap: 40px;
    position: relative;
}

.login__link {
    transition: all 0.3s ease;
    color: #4285f4;
    text-decoration: none;
    line-height: 0;
    max-width: 38px;
    padding-left: 4px;
}

.login__link:hover {
    opacity: 0.7;
}

.login__logo {
    transition: all 0.3s ease;
    width: 38px;
    height: 38px;
}

.login__logo:hover {
    transform: rotate(45deg);
}

.login__title {
    font-weight: 500;
    font-size: 24px;
    line-height: 1.21;
    margin: 0;
}

.login__labels-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 20px;
}

.login__label {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 3px;
    cursor: pointer;
    position: relative;
}

.login__label-text {
    font-weight: 400;
    font-size: 10px;
    line-height: 1.2;
    color: #8b8b8b;
    margin-bottom: 7px;
}

.login__input {
    padding: 15px 15px;
    box-sizing: border-box;
    border-style: none;
    font-weight: 400;
    font-size: 13px;
    line-height: normal;
    background-color: rgba(47, 47, 47, 1);
    color: #fff;
    border-radius: 8px;
}

.login__input:focus {
    outline: 1px solid #3ddc84;
}

.login__input_error:focus {
    outline: 1px solid #ee3465;
}

.login__input_error {
    color: rgba(238, 52, 101, 1);
}

.login__input[type='email'] {
    font-weight: 500;
}

.login__error {
    font-weight: 400;
    font-size: 10px;
    line-height: 1.2;
    color: rgba(238, 52, 101, 1);
    position: absolute;
    top: 74px;
    left: 0;
}

.login__button {
    margin-top: auto;
    border-style: none;
    background-color: #4285f4;
    border-radius: 3px;
    color: #fff;
    font-size: 14px;
    line-height: 1.21;
    width: 100%;
    padding: 14px 0;
    box-sizing: border-box;
    transition: all 0.3s ease;
    cursor: pointer;
    font-weight: 500;
}

.login__button:hover {
    opacity: 0.8;
}

.login__button_disabled {
    background-color: #f8f8f8;
    color: #c2c2c2;
    cursor: default;
}

.login__button_disabled:hover {
    opacity: 1;
}

.login__support {
    font-weight: 400;
    font-size: 14px;
    line-height: 1.21;
    color: #8b8b8b;
    text-align: center;
    position: absolute;
    bottom: -7%;
    left: 14%;
}