.not-found {
    display: flex;
    min-height: 100vh;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    font-family: 'Inter', 'SuisseIntl', sans-serif;
}

.not-found__text-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
    margin: 0;
}

.not-found__error {
    font-weight: 400;
    font-size: 140px;
    line-height: 1.21;
}

.not-found__error-name {
    font-weight: 400;
    font-size: 16px;
    line-height: 1.19;
}

.not-found__button {
    margin-top: 186px;
    border: none;
    background-color: transparent;
    cursor: pointer;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.21;
    color: #4285f4;
    padding: 0;
    bottom: 60px;
}

.not-found__button:hover {
    opacity: 0.8;
}